* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-weight: 300;
}

body {
  font-family: "Source Sans Pro", sans-serif;
    color: #001d3b;
    font-weight: 300;
    background: #ffe7d6;
    width: 100%;
    height: 100%;
}

.container {
  margin: 4% auto;
  padding: 40px 0;
  text-align: center;
}

.container h1 {
  font-size: 40px;
  font-weight: 200;
}
button {  
  background-color: #03436f;
  border: 0;
  padding: 10px 15px;
  color: #fff;
  border-radius: 3px;
  width: 250px;
  font-size: 18px;
  cursor: pointer;
}


button:disabled{
  background-color: #cbc2c2;
  cursor:not-allowed;
}
a{
  color: #001d3b;
  font-size: medium;
}